#load-screen {
  opacity: 1;
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: var(--bla1);
  z-index: 999999;
  -webkit-transition: height 1s;
  transition: height 1s;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  overflow: hidden;
}

#load-screen h2 {
  font-family: 'Baloo', cursive;
  font-size: 4em;
  color:var(--w);
}

#load-screen .load-animation {
   width: 3em;
   height: 3em;
   background-position: center;
   background-repeat: no-repeat;
   background-size: cover;
   background-image: url('https://firebasestorage.googleapis.com/v0/b/notnik-app.appspot.com/o/assets%2Floader.svg?alt=media&token=a6f92cda-7f6f-4f6b-9f60-c319e2af8553');
