.Start--Buton:focus {
	background-color:var(--bla3);
}

.List__Entries {
	margin-left: 2em;
	margin-right: 2em;
	width: calc(100vw - 10em);
	padding: 2em 0;
	height: auto;
	display: grid;
	grid-template-rows: repeat(4, 1fr);
	grid-row-gap: 1.5em;
	grid-column-gap: 1.5em;
	grid-auto-flow: column;
	padding-left: 2em;
	overflow-x: scroll;
}
.List__Entries a {
	width: 30em;
	text-decoration: none;
	height: 10em;
}

.List__Entries::-webkit-scrollbar {
	height: 0.7em;
	width: 0.7em;
	cursor: e-resize;
}

.List__Entries::-webkit-scrollbar-track {
	background:var(--bla1);
}
.List__Entries::-webkit-scrollbar-thumb {
	background:var(--bla4);
}
.List__Entries::-webkit-scrollbar-thumb:hover {
	background:var(--bla3);
}

.copy {
	top:50%;
	-webkit-transform:rotateZ(-90deg) translateY(-50%);
	        transform:rotateZ(-90deg) translateY(-50%);
	right:-8em;
	position: absolute;
	font-size: 0.5em;
	color: var(--w);
}

.Login {
	position: absolute;
	right: 2em;
	top:3em;
	cursor: pointer;
	z-index: 9999;
}

.Login h2 {
	font-family: 'Baloo', cursive;
	font-size:0.9em;
	color:var(--w);
	line-height: 0em;
	outline: none;
}

.Login h2:hover {
	text-shadow: 0 0 20px var(--w);
}

.disableStart {
	opacity: 0 !important;
	pointer-events: none !important;
}
.FullEntry {
	position: relative;
	width: 100%;
	height: 100%;
	background-color:var(--bla2);
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: 11em 1fr;
	overflow: hidden;
}
.Splash {
	width: 100%;
	height: 100%;
	background: var(--bla3);
	display: grid;
	grid-template-columns: 27em 1fr;
	grid-template-rows: 1fr;
	background-image: url('https://firebasestorage.googleapis.com/v0/b/notnik-app.appspot.com/o/assets%2FNotnik-bg-splash.jpg?alt=media&token=6b6e10ac-5fec-4e5d-950f-b05b30f30be9');
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	-webkit-animation: zoom 30s ease-in-out infinite;
	        animation: zoom 30s ease-in-out infinite;
}

@-webkit-keyframes zoom {
	0% {
		background-size: 300%;
	}
	50% {
		background-size: 350%;
	}
	100% {
		background-size: 300%;
	}
}

@keyframes zoom {
	0% {
		background-size: 300%;
	}
	50% {
		background-size: 350%;
	}
	100% {
		background-size: 300%;
	}
}

.Splash__Left {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-flex-direction: column;
	        flex-direction: column;
}

.Splash__Left .Splash__Left__Text {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-flex-direction: column;
	        flex-direction: column;
	padding: 3em;
	/*border:1px solid var(--or); // ramka? :D */
}

.Splash__Left .Splash__Left__Text h2 {
	font-family: 'Baloo', cursive;
	font-size: 4.5em;
	line-height: 0em;
	outline: none;
	color: var(--w);
}

.Splash__Left .Splash__Left__Text h3 {
	position: relative;
	font-family: 'Montserrat', sans-serif;
	margin:-1.8em 0 2em 0;
	font-weight: 700;
	font-size: 0.6em;
	width: 20em;
	color: var(--or);
	outline: none;
	text-align: center;
}

.Splash__Left .Splash__Left__Text h3:after {
	position: absolute;
	content: '';
	width: 10em;
	height: 0.1em;
	background-color: var(--w);
	top:200%;
	left:50%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
}

.Splash__Left .Splash__Left__Text p {
	position: relative;
	font-family: 'Montserrat', sans-serif;
	line-height: 2em;
	font-size: 0.8em;
	font-weight: 400;
	width: 20em;
	color: var(--w);
	outline: none;
	text-align: justify;
	z-index:999;
	text-indent:2em;
}

.Splash__Right {
	display: -webkit-flex;
